const paths = {
  plane:
    'M187.57,0C291.16,0,375.13,67.75,375.13,151.32c0,33.35-13.41,64.17-36.07,89.17l34.85,86.65L226.21,297.46c-15.43.42-32.72,4.16-50,4.61-2,.05-3.92.08-5.86.08C69.88,302.15,0,233.3,0,151.32,0,67.75,84,0,187.57,0m0,41C106.76,41,41,90.51,41,151.32c0,62.6,55.57,109.8,129.28,109.8q2.38,0,4.78-.06c6.93-.18,14.5-1.12,22.52-2.12,8.87-1.1,18-2.24,27.49-2.5l4.64-.13,4.56.92,73.19,14.71L301,255.81,291.41,232l17.25-19c11.61-12.81,25.44-33.89,25.44-61.62C334.1,90.51,268.37,41,187.57,41Z',
  heart:
    'M308.31,30.92H292.1v-4.8A26.2,26.2,0,0,0,266,0h-.39a26.2,26.2,0,0,0-26.13,26.13v4.8H95v-4.8A26.2,26.2,0,0,0,68.83,0h-.39A26.2,26.2,0,0,0,42.31,26.13v4.8H26.13A26.2,26.2,0,0,0,0,57.05v249.8A26.2,26.2,0,0,0,26.13,333H308.31a26.2,26.2,0,0,0,26.13-26.13V57.05A26.2,26.2,0,0,0,308.31,30.92Zm-58.4-4.8a15.69,15.69,0,0,1,15.68-15.68H266a15.71,15.71,0,0,1,15.68,15.68V72.61A15.71,15.71,0,0,1,266,88.28h-.39a15.69,15.69,0,0,1-15.68-15.68Zm-197.14,0A15.69,15.69,0,0,1,68.44,10.45h.39A15.71,15.71,0,0,1,84.51,26.13V72.61A15.71,15.71,0,0,1,68.83,88.28h-.39A15.69,15.69,0,0,1,52.77,72.61Zm255.54,286H26.13a5.3,5.3,0,0,1-5.23-5.23V123.19H313.53V306.86A5.3,5.3,0,0,1,308.31,312.08Z',
  bookmark:
    'M357,0H102C73.95,0,51,22.95,51,51v408l178.5-76.5L408,459V51C408,22.95,385.05,0,357,0z',
}
const text = {
  first: 'Refreshingly Simple Text Message Appointment Reminders',
  second: 'Refreshingly Simple Text Message Appointment Reminders',
  third: 'Refreshingly Simple Text Message Appointment Reminders',
}
const textIds = Object.keys(text)
const pathIds = Object.keys(paths)

const morphTransition = ({ from, to }) =>
  tween({
    from: 0,
    to: 1,
  }).pipe(interpolate(from, to))

const Icon = posed.path(
  pathIds.reduce((config, id) => {
    config[id] = {
      d: paths[id],
      transition: morphTransition,
    }

    return config
  }, {}),
)

const NextButton = posed.button({
  hoverable: true,
  pressable: true,
  init: { scale: 1 },
  hover: { scale: 1.1 },
  press: { scale: 0.9 },
})

class IconMorph extends React.Component {
  state = {
    pathIndex: 0,
    buttonText: '',
  }

  gotoNext = () => {
    const { pathIndex } = this.state
    const nextIndex = pathIndex + 1
    this.setState({
      pathIndex: nextIndex > pathIds.length - 1 ? 0 : nextIndex,
    })
  }

  nextText = () => {
    const { buttonText } = this.state
    const nextText = buttonText + 1
    this.setState({
      buttonText: nextText > textIds.length - 1 ? 0 : nextText,
    })
  }

  render() {
    return (
      <StyledBody>
        <svg
          width="120"
          height="120"
          viewBox="0 0  375.13 327.15"
          fill="rgb(70, 200, 120)"
        >
          <Icon pose={pathIds[this.state.pathIndex]} />
        </svg>
        <NextButton onClick={this.nextText} onClick={this.gotoNext}>
          {this.state.buttonText}
        </NextButton>
      </StyledBody>
    )
  }
}

export default IconMorph
